<template>
  <div class="interfaceManagement info-bg">
    <div class="searchContent info-box">
      <div>
        <div>姓名</div>
        <div>
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </div>
      </div>
      <div>
        <div>github账号</div>
        <div>
          <el-input v-model="form.github" placeholder="请输入github"></el-input>
        </div>
      </div>
      <div>
        <div>邮箱前缀</div>
        <div>
          <el-input v-model="form.email" placeholder="请输入邮箱前缀"></el-input>
        </div>
      </div>
      <div>
        <div>组</div>
        <div>
          <el-select v-model="form.group" placeholder="请选择组">
            <el-option
              v-for="item in options.group"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="searchBtn">
        <el-button type="primary" @click="search">查询</el-button>
        <el-button @click="reset">重置</el-button>
      </div>
    </div>
    <div class="tableContent info-box">
      <div class="tableBtn">
        <el-button type="primary" @click="dialogAdd">新增</el-button>
      </div>
      <el-table
        :data="data"
        border
        style="width: 100%; margin: 10px 0;">
        <el-table-column
          type="index"
          label="序号"
          align="center"
          width="55">
        </el-table-column>
        <el-table-column
          label="创建时间"
          align="center"
          prop="create_time">
        </el-table-column>
        <el-table-column
          label="姓名"
          prop="name"
          align="center">
        </el-table-column>
        <el-table-column
          label="github账号"
          prop="github"
          align="center">
        </el-table-column>
        <el-table-column
          label="邮箱前缀"
          prop="email"
          align="center">
        </el-table-column>
        <el-table-column
          label="组"
          prop="group"
          align="center">
        </el-table-column>
        <el-table-column
          label="操作"
          width="120"
          align="center">
          <template slot-scope="scope">
            <a @click="showDialog(scope.row.id,'show')">查看</a>
            <a @click="editDialog(scope.row.id)">编辑</a>
            <a @click="deleteData(scope.row.id)">删除</a>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="page_size"
        @current-change="currentChange">
      </el-pagination>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      :show-close="false"
      width="30%"
      :before-close="handleClose">
      <el-form :model="dialogForm" :rules="rules" ref="ruleForm">
        <el-form-item label="姓名" prop="name" :label-width="formLabelWidth">
          <el-input v-model="dialogForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="github账号" prop="github" :label-width="formLabelWidth">
          <el-input v-model="dialogForm.github" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="email邮箱" prop="email" :label-width="formLabelWidth">
          <el-input v-model="dialogForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="组" prop="group" :label-width="formLabelWidth">
          <el-select v-model="dialogForm.group" placeholder="请选择组">
            <el-option
              v-for="item in options.group"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelDialog">取消</el-button>
        <el-button type="primary" @click="saveDialog">确定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="查看"
      :visible.sync="showDialogVisible"
      width="30%">
      <el-form :model="dialogForm" :rules="rules" ref="ruleForm">
        <el-form-item label="姓名" :label-width="formLabelWidth">
          {{ dialogForm.name }}
        </el-form-item>
        <el-form-item label="github账号" :label-width="formLabelWidth">
          {{ dialogForm.github }}
        </el-form-item>
        <el-form-item label="email邮箱" :label-width="formLabelWidth">
          {{ dialogForm.email }}
        </el-form-item>
        <el-form-item label="组" :label-width="formLabelWidth">
          {{ dialogForm.group }}
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
      </span>
    </el-dialog>
  </div>
  
</template>
<script>
  export default {
    name: 'interfaceManagement',
    data () {
      return {
        form: {
          name: '',
          github: '',
          email: '',
          group: '',
        },
        data: [],
        options: {
          group: [
            {
              value: '框架',
              label: '框架'
            },
            {
              value: '模型',
              label: '模型'
            },
            {
              value: 'PM',
              label: 'PM'
            },
            {
              value: '分布式',
              label: '分布式'
            },
            {
              value: '深研',
              label: '深研'
            },
            
          ]
        },
        dialogVisible: false,
        showDialogVisible: false,
        dialogForm: {
          name: '',
          github: '',
          email: '',
          group: '',
        },
        formLabelWidth: '90px',
        page: 1,
        page_size: 12,
        total: 0,
        rules: {
          name: [
            { required: true, message: '请输入名称', trigger: 'blur' },
          ],
          github: [
            { required: true, message: '请输入github', trigger: 'blur' },
          ],
          email: [
            { required: true, message: '请输入email', trigger: 'blur' },
          ],
          group: [
            { required: true, message: '请选择组', trigger: 'change' },
          ]
        },
        type: 'add',
      }
    },
    computed: {
      dialogTitle() {
        return this.type === 'add'? '新增' : '编辑'
      }
    },
    methods: {
      handleClose() {
        this.dialogVisible = false;
      },
      dialogAdd () {
        this.dialogVisible = true;
        this.type = 'add';
      },
      currentChange(current) {
        this.page = current;
        this._search();
      },
      search() {
        this.page = 1;
        this._search();
      },
      _search() {
        this.form.page = this.page;
        this.form.page_size = this.page_size;
        this.$http.get('/v1/personnel',{
          params: this.form
        }).then(res=>{
          if ( res.status === 200 ) {
            this.data = res.data.content;
            this.total = res.data.totalElements;
          }
        })
      },
      reset() {
        for ( let i in this.form ) {
          this.form[i] = '';
        }
        this.page = 1;
        this.page_size = 12;
        this._search();
      },
      // 新增 or 编辑保存
      saveDialog() {
        this.dialogForm['github_w'] = this.dialogForm.github;
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            // 新增
            if ( this.type === 'add' ) {
              this.$http.post('/v1/personnel',this.dialogForm).then(res=>{
                if ( res.status === 201 ) {
                  this.$message({
                    message: '保存成功',
                    type: 'success'
                  });
                  this.cancelDialog();
                  this.search();
                } else {
                  this.$message.error(res.data.scheme.message);
                }
              })
            } else {
              // 编辑保存
              this.$http.put(`/v1/personnel/${this.dialogForm['id']}`,this.dialogForm).then(res=>{
                if ( res.status === 200 ) {
                  this.$message({
                    message: '保存成功',
                    type: 'success'
                  });
                  this.cancelDialog();
                  this.search();
                } else {
                  this.$message.error(res.data.scheme.message)
                }
              })
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      // 取消Dialog
      cancelDialog() {
        this.$refs['ruleForm'].resetFields();
        this.dialogForm = {
          name: '',
          github: '',
          email: '',
          group: '',
        }
        this.dialogVisible = false;
      },
      // 查看
      showDialog(id,type) {
        this.type = type;
        this.$http.get(`/v1/personnel/${id}`).then(res=>{
          if ( res.status === 200 ) {
            this.dialogForm = res.data;
            console.log(this.type)
            if (this.type == 'edit') {
              this.dialogVisible = true;
              return;
            }
            this.showDialogVisible = true;
          }
        })
      },
      // 编辑
      editDialog(id) {
        this.showDialog(id,'edit');
      },
      deleteData(id) {
        this.$http.delete(`/v1/personnel/${id}`).then(res=>{
          if ( res.status === 204 ) {
            this.search();
            this.$message.success('删除成功');
          }
        })
      },
    },
    mounted() {
      this._search();
    }
  }
</script>
<style lang="less">
  .interfaceManagement {
    padding: 20px;
  }
</style>


